
export default function getContentPerTypeService(typeList, content) {
    /* Pour afficher l'article correctement, dans l'ordre et de faon scure,
   on veut un tableau sous la forme {type, contenu} pour afficher l'article dans le bon ordre par type d'lment */
    const contentPerType = [];
    let startPosition = 0;
    // Pour cela, on commence par boucler tant qu'on a pas parcouru l'entiret du coutenu
    while(startPosition < content.length) {

        // Puis on appelle cette fonction qui donne le premier lment depuis l'index startPos ainsi que
        // la position de fin de cet lment ( partir de laquelle on va repartir pour chercher d'autres lments
        const res = getFirstElementFromStartPos(startPosition, typeList, content);

        const {type, element, startPos} = res;
        startPosition = startPos;

        contentPerType.push({type: type, value: element});
    }
    return contentPerType;
}

function getFirstElementFromStartPos(startPos, typeList, content) {
    /* Cette fonction renvoie un tableau contenant la position de tous les lments dfinis dans le constructeur depuis startPos */
    const elementPos = getAllElementsPos(startPos, typeList, content);

    // On rcupre la position de l'lment le plus proche
    let nearestTagPos = Math.min(...(elementPos).filter((pos) => pos !== -1));


    if (nearestTagPos === Infinity) {  // Math.min renvoie infinity si aucune balise correspondante n'a t trouve
        nearestTagPos = content.length;
    }

    // On rcupre son type
    let libelleType = typeList[elementPos.indexOf(nearestTagPos)];

    let element = '';
    let nextStartPos;
    if (nearestTagPos === 0 || nearestTagPos === startPos) {        // On enlve la balise de dpart si on vient de dtecter une balise

        nearestTagPos +=('[' + libelleType + ']').length;
        const endPos = (nearestTagPos + (content.substring(nearestTagPos)).indexOf('[/' + libelleType + ']'));

        element = content.substring(nearestTagPos, endPos);
        nextStartPos = endPos + ('[/' + libelleType + ']').length;
    }
    else  {     // Si la position trouve est diffrente de zro : on est dans du contenu interne  une balise racine
        // On rcupre le contenu en faisant un substring
        element = content.substring(startPos, nearestTagPos);
        nextStartPos = nearestTagPos;
        libelleType = undefined;
    }

    // Qu'on renvoie au render, tout en donnant la position de fin (aprs la balise de fermeture
    return {type: libelleType, element, startPos: nextStartPos};
}

function getAllElementsPos(startPos, typeList, content) {
    const elementPos = [];
    // Pour chaque type dclar dans le constructeur, on rcupre la position du premier lment qu'on trouve  partir de "startPos"
    typeList.forEach((type) => {
        elementPos.push(content.indexOf('[' + type + ']', startPos));
    });

    return elementPos;
}